'use client';

import { useMemo, useState } from 'react';
import { Plus, Search, Filter, Trash2 } from 'lucide-react';
import clsx from 'clsx';

const statusTone = {
  Planned: 'bg-slate-100 text-slate-600',
  'In Progress': 'bg-blue-100 text-blue-700',
  'At Risk': 'bg-amber-100 text-amber-700',
  Completed: 'bg-green-100 text-green-700',
  'On Hold': 'bg-orange-100 text-orange-700',
  Cancelled: 'bg-red-100 text-red-600',
};

const formatDate = (value) => {
  if (!value) return 'No deadline';
  return new Intl.DateTimeFormat('en', { month: 'short', day: 'numeric' }).format(new Date(value));
};

export default function ProjectDashboard({
  projects = [],
  selectedProjectId,
  onSelect = () => {},
  onCreate = async () => {},
  onDelete = () => {},
  onProgressUpdate = async () => {},
  busyProjectIds = new Set(),
}) {
  const [form, setForm] = useState({
    name: '',
    studio: 'Core Studio',
    dueDate: '',
    color: '#1d4ed8',
  });
  const [showForm, setShowForm] = useState(false);
  const [progressDrafts, setProgressDrafts] = useState({});
  const [submitting, setSubmitting] = useState(false);
  const [search, setSearch] = useState('');

  const filtered = useMemo(() => {
    if (!search.trim()) return projects;
    return projects.filter((project) =>
      project.name?.toLowerCase().includes(search.trim().toLowerCase()),
    );
  }, [projects, search]);

  const totals = useMemo(() => {
    if (!projects.length) {
      return { average: 0, active: 0, completed: 0 };
    }
    const completed = projects.filter((p) => p.status === 'Completed').length;
    const active = projects.length - completed;
    const average = Math.round(
      projects.reduce((acc, project) => acc + (project.progress || 0), 0) / projects.length,
    );

    return { average, active, completed };
  }, [projects]);

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!form.name.trim()) return;
    try {
      setSubmitting(true);
      await onCreate(form);
      setForm({
        name: '',
        studio: 'Core Studio',
        dueDate: '',
        color: '#1d4ed8',
      });
      setShowForm(false);
    } finally {
      setSubmitting(false);
    }
  };

  const handleProgressChange = (projectId, value) => {
    setProgressDrafts((prev) => ({
      ...prev,
      [projectId]: value,
    }));
  };

  const commitProgress = async (projectId) => {
    const value = progressDrafts[projectId];
    if (value === undefined) return;
    try {
      await onProgressUpdate(projectId, value);
    } finally {
      setProgressDrafts((prev) => {
        const copy = { ...prev };
        delete copy[projectId];
        return copy;
      });
    }
  };

  return (
    <section className="surface-card space-y-6">
      <div className="flex items-center justify-between mb-6">
        <div className="relative flex-1 max-w-md">
          <Search size={16} className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400" />
          <input
            className="w-full border-slate-200 pl-10 py-2 rounded-lg"
            placeholder="Search projects"
            value={search}
            onChange={(event) => setSearch(event.target.value)}
          />
        </div>
        <div className="flex items-center gap-3">
          <button type="button" className="btn-secondary text-sm">
            <Filter size={16} />
          </button>
          <button className="p-2 hover:bg-slate-100 rounded-lg">
            <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <path d="M3 12h18m-9-9v18"/>
            </svg>
          </button>
        </div>
      </div>

      <div className="overflow-hidden rounded-2xl border border-slate-200 bg-white">
        <table className="table-card text-sm">
          <thead>
            <tr>
              <th>PROJECT NAME</th>
              <th>STATUS</th>
              <th>PROGRESS</th>
              <th>ACTIONS</th>
            </tr>
          </thead>
          <tbody>
            {filtered.length ? (
              filtered.map((project) => {
                const isSelected = project._id === selectedProjectId;
                const rowProgress = progressDrafts[project._id] ?? project.progress ?? 0;
                const busy = busyProjectIds?.has?.(project._id) ?? false;

                return (
                  <tr
                    key={project._id}
                    className={clsx(
                      'cursor-pointer transition hover:bg-slate-50',
                      isSelected && 'bg-slate-50',
                    )}
                    onClick={() => onSelect(project._id)}
                  >
                    <td>
                      <div className="font-semibold text-slate-900">{project.name}</div>
                    </td>
                    <td>
                      <span
                        className={clsx(
                          'status-pill',
                          statusTone[project.status] || 'bg-slate-100 text-slate-600',
                        )}
                      >
                        {project.status}
                      </span>
                    </td>
                    <td>
                      <div className="flex items-center gap-3">
                        <div className="h-2 flex-1 rounded-full bg-slate-200">
                          <div
                            className={clsx(
                              'h-full rounded-full',
                              project.status === 'Completed' ? 'bg-green-500' :
                              project.status === 'In Progress' ? 'bg-blue-500' :
                              project.status === 'On Hold' ? 'bg-orange-500' :
                              project.status === 'Cancelled' ? 'bg-red-500' : 'bg-gray-400'
                            )}
                            style={{ width: `${rowProgress}%` }}
                          />
                        </div>
                        <span className="w-12 text-right text-sm font-medium text-slate-900">{rowProgress}%</span>
                      </div>
                    </td>
                    <td>
                      <div className="flex items-center gap-2">
                        <button
                          type="button"
                          onClick={(event) => {
                            event.stopPropagation();
                          }}
                          className="p-1 text-slate-400 hover:text-slate-600"
                        >
                          <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                            <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"/>
                            <path d="m18.5 2.5 a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"/>
                          </svg>
                        </button>
                        <button
                          type="button"
                          onClick={(event) => {
                            event.stopPropagation();
                            onDelete(project._id);
                          }}
                          className="p-1 text-slate-400 hover:text-red-500"
                        >
                          <Trash2 size={16} />
                        </button>
                      </div>
                    </td>
                  </tr>
                );
              })
            ) : (
              <tr>
                <td colSpan={4} className="py-10 text-center text-slate-500">
                  No projects yet. Use the button above to add your first project.
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>

      {showForm && (
        <form
          id="project-create"
          onSubmit={handleSubmit}
          className="grid gap-4 rounded-2xl border border-slate-200 bg-slate-50/60 p-5 md:grid-cols-2"
        >
          <label className="flex flex-col gap-1 text-sm text-slate-600">
            Project name
            <input
              required
              placeholder="Seaside Villa"
              value={form.name}
              onChange={(event) => setForm((prev) => ({ ...prev, name: event.target.value }))}
            />
          </label>
          <label className="flex flex-col gap-1 text-sm text-slate-600">
            Studio
            <input
              placeholder="Core Studio"
              value={form.studio}
              onChange={(event) => setForm((prev) => ({ ...prev, studio: event.target.value }))}
            />
          </label>
          <label className="flex flex-col gap-1 text-sm text-slate-600">
            Due date
            <input
              type="date"
              value={form.dueDate}
              onChange={(event) => setForm((prev) => ({ ...prev, dueDate: event.target.value }))}
            />
          </label>
          <label className="flex flex-col gap-1 text-sm text-slate-600">
            Accent color
            <input
              type="color"
              className="h-11 cursor-pointer rounded-xl border border-slate-200"
              value={form.color}
              onChange={(event) => setForm((prev) => ({ ...prev, color: event.target.value }))}
            />
          </label>
          <div className="md:col-span-2 flex justify-end">
            <button type="submit" disabled={submitting} className="btn-primary">
              <Plus size={16} />
              {submitting ? 'Creatingâ€¦' : 'Create project'}
            </button>
          </div>
        </form>
      )}
    </section>
  );
}
